import { lazy } from "react";
import IntroductionContent from "../content/LoreIntroductionContent.json";
import Lore1Content from "../content/Lore1Content.json";
import Lore2Content from "../content/Lore2Content.json";
import ConclusionContent from "../content/LoreConclusionContent.json";

const Container = lazy(() => import("../common/Container"));
const ContentBlock = lazy(() => import("../components/ContentBlock"));

const Lore = () => {
  return (
    <Container>
      <ContentBlock
        type="right"
        title={IntroductionContent.title}
        content={IntroductionContent.text}
        icon={`snakes-on-chain-website/img/svg/lore.svg`}
        id="intro"
      />
      <ContentBlock
        type="left"
        title={Lore1Content.title}
        content={Lore1Content.text}
        icon={`snakes-on-chain-website/img/svg/lore1.svg`}
        id="lore1"
      />
      <ContentBlock
        type="right"
        title={Lore2Content.title}
        content={Lore2Content.text}
        icon={`snakes-on-chain-website/img/svg/lore2.svg`}
        id="lore2"
      />
      <ContentBlock
        type="left"
        title={ConclusionContent.title}
        content={ConclusionContent.text}
        icon={`snakes-on-chain-website/img/svg/lore3.svg`}
        id="conclusion"
      />
    </Container>
  );
};

export default Lore;
